import { googleLogout, useGoogleLogin } from '@react-oauth/google';
import React, { useState, useEffect } from "react"
import axios from "axios"

const folderId = "1HFYPRZx2O7WNJAQz1Lc_sUzC6TuEojl2"; // folder images ca bn

export default function DriveImages(){
  const [images, setImages] = useState([]);

  const login = useGoogleLogin({
    flow: 'implicit',
    scope: 'https://www.googleapis.com/auth/drive.readonly',
    onSuccess: async (tokenResponse) => {
      const accessToken = tokenResponse.access_token;
      console.log("ac:", accessToken)
      const res = await axios.get(
        `https://www.googleapis.com/drive/v3/files?q='${folderId}' in parents and mimeType contains 'image/'&fields=files(id,name)&spaces=drive`,
        { headers: { Authorization: `Bearer ${accessToken}` } }
      );
      
      const files = res.data.files.map(f => ({
        id: f.id,
        url: `https://lh3.googleusercontent.com/d/${f.id}=s2000`  // hnh full size
      }));
      setImages(files);
    }
  });
  useEffect(() => {
  login()
}, [])
  return(
    <div>
      <button onClick={() => login()}>Login Google Drive</button>

      <div style={{
        display:'grid',
        gridTemplateColumns:'repeat(4,1fr)',
        gap:10,
        marginTop:20
      }}>
        {images.map(img => (
          <img key={img.id} src={img.url} style={{width:'100%',borderRadius:"8px"}} />
        ))}
      </div>
    </div>
  );
}
