import React, { useEffect, useState } from 'react'

import styled from 'styled-components'
import Title from './SectionTitle'
import FrameImage from '../assets/imgs/frame.png'
import config from '../config'

const conf = config.Couple;
const StyledWrapper = styled.section`
  width: 100%;
  background-color: #fff;
  padding: 0.3rem 0;
  .cp {
    display: flex;
    align-items: center;
    justify-content: center;
    @media screen and (max-width: 414px) {
      flex-direction: column;
      margin: 0;
    }
    .profile {
      color: #222;
      padding: 0.4rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-right: 0.4rem;
      .pic {
        width: 2.8rem;
        height: 2.8rem;
        background-repeat: no-repeat;
        background-size: 80%, 100%;
        background-position: center;
        overflow: hidden;
        &.boy {
          background-image: url(${conf.boy.image}),
            url(${FrameImage});
          background-size: 75%, 100%;
          background-position-y: 5px;
        }
        &.girl {
          background-image: url(${conf.girl.image}),
            url(${FrameImage});
        }
        img {
          width: 100%;
          height: 100%;
        }
      }
      .name {
        font-family: 'SP-F';
        font-size: 0.4rem;
        padding: 0.2rem 0;
        margin: 0.2rem 0;
      }
      .intro {
        font-size: 0.12rem;
        white-space: nowrap;
      }
    }
  }
`

const profile = {
  boy:{
    name: 'Manh',
    description:'Nguyen Dinh Manh'
  },
  girl:{
    name: 'Huong',
    description:'Nguyen Thi Huong'
  }
}
export default function Couple({ popupDan }) {
  const [pos, setPos] = useState('h')
  useEffect(() => {
    if (typeof window !== 'undefined') {
      if (window.innerWidth <= 414) {
        setPos('v')
      }
    }
  }, [])
  const handleDC = () => {
    popupDan('666')
  }
  return (
    <StyledWrapper>
      <Title title="MANH vs M" />
      <div className="cp">
        <div className="profile">
          <div className="pic boy" onDoubleClick={handleDC}>
            <img src={FrameImage} alt="man" />
          </div>
          <div className="name">{profile.boy.name}</div>
          <div className="intro">
            {profile.boy.description}
          </div>
        </div>
        <div className="profile">
          <div className="pic girl" onDoubleClick={handleDC}>
            <img src={FrameImage} alt="man" />
          </div>
          <div className="name">{profile.girl.name}</div>
          <div className="intro">
            {profile.girl.description}
          </div>
        </div>
      </div>
    </StyledWrapper>
  )
}
